
import {
    Component,
    OnDestroy,
    OnInit,
    Inject,
    Directive
} from '@angular/core';

import {
    DOCUMENT,
    Title
} from '@angular/platform-browser';

import { List } from 'linqts';

import {
    TokenEntry_Enum,
    EnumApps,
    SubMenuGroup
} from '../Shared/AtParEnums';

import { SpinnerService } from '../components/spinner/event.spinner.service';
import { LeftBarAnimationService } from './trackit.leftbar-animation.service';
import { Menus } from '../AtPar/Menus/routepath';
import { MT_ATPAR_APP } from '../Entities/MT_ATPAR_APP';
import { MT_ATPAR_APP_GROUP } from '../Entities/MT_ATPAR_APP_GROUP';
import { Message } from '../components/common/api';
import { AtParConstants } from '../Shared/AtParConstants';
import { HttpService } from '../Shared/HttpService';

/**
*	This class represents the lazy loaded DashboardComponent.
*/
declare var module: {
    id: string;
}


@Component({

    selector: 'trackit-home-cmp',
    template: require('./trackit.dashboard.component.html'),
    providers: [
        AtParConstants,
        LeftBarAnimationService
    ]

})

export class TrackITDashboardComponent implements OnInit {

    showAdmin: boolean = true;
    isModuleActive: boolean;

    activeMenu: String;
    showStyle: String;
    hideStyle: String;
    moduleName: string;
    activeddName: string;
    Menu: string;

    menu: Menus;
    lstModuleItems: any;
    appModules: List<MT_ATPAR_APP>;
    groupModules: List<MT_ATPAR_APP>;
    activeGroup: MT_ATPAR_APP_GROUP;
    lstMenus: List<Menus>;
    moduleMenus: List<Menus>;
    lstGroupModuleMenus: List<Menus>;
    msgs: Message[] = [];
    menudata: any;

 
  




    constructor(
        private leftBarAnimateService: LeftBarAnimationService,
        private title: Title,
        @Inject(DOCUMENT) private document,
        private atParConstant: AtParConstants,
        private spinnerservice: SpinnerService,
        private httpService: HttpService
    ) {
        try {
            
            this.title.setTitle('Dashboard');
            this.activeddName = '';
            this.showStyle = "block";
            this.hideStyle = "none";
            this.isModuleActive = false;
            this.groupModules = new List<MT_ATPAR_APP>();
            this.moduleName = 'none';
            this.moduleMenus = new List<Menus>();

            leftBarAnimateService.changeEmitted$.subscribe(
                groupModules => {
                    this.groupModules = new List<MT_ATPAR_APP>();
                    this.groupModules = groupModules;
                }
            );

            leftBarAnimateService.changeEmittedActiveAppName.subscribe(
                moduleName => {
                    this.moduleName = moduleName;
                   
                }
            )


        } catch (ex) {
            this.clientErrorMsg(ex);
        }
    }

    ngOnInit() {
        try {
            this.getMenu();

        } catch (ex) {
            this.clientErrorMsg(ex);
        }
    }

    getMenu() {
        try {
            return this.showStyle;
        } catch (ex) {
            this.clientErrorMsg(ex);
        }
    }



    appModuleChange(activeModuleName) {
        this.moduleName = activeModuleName;
    }



    onHomeClick() {
        this.activeddName = '';
        this.Menu = '';

    }



    profileClickChanged(activeddName) {
        this.activeddName = activeddName;
    }

    filteredData(searchString) {
        this.Menu = searchString;
    }

    clientErrorMsg(ex) {
     
    }

    divClick() {
       
    }
    mobileDisplay() {
        AtParConstants.count = !AtParConstants.count;
        if (AtParConstants.count) {
            this.document.getElementById('main').style.margin = "0 0 0 0px";
            this.document.getElementById('leftsidebar').style.width = "0px";
            this.document.getElementById('menu-icon').style.left = "0px";
            for (var i = 0; i < document.getElementsByClassName('drop1').length; i++) {
                var div = document.getElementsByClassName('drop1')[i]
                div.setAttribute("style", "display:" + "none");
            }
            for (var i = 0; i < document.getElementsByClassName('breadcrumb').length; i++) {
                var div = document.getElementsByClassName('breadcrumb')[i]
                div.setAttribute("style", "left:" + "34px");
            }


        } else {
            this.document.getElementById('main').style.margin = "";
            this.document.getElementById('leftsidebar').style.display = "block";
            this.document.getElementById('leftsidebar').style.width = "";
            this.document.getElementById('menu-icon').style.left = "175px";
            for (var i = 0; i < document.getElementsByClassName('drop1').length; i++) {
                var div = document.getElementsByClassName('drop1')[i]
                div.setAttribute("style", "display:" + "none");
            }
            for (var i = 0; i < document.getElementsByClassName('breadcrumb').length; i++) {
                var div = document.getElementsByClassName('breadcrumb')[i]
                div.setAttribute("style", "left:" + "194px");
            }
        }

        this.moduleName = 'none';

    }


    OnDestroy() {


    }

}




