
import {
    Component,
    OnInit,
    OnDestroy,
} from '@angular/core';

import { Router} from '@angular/router';

import { SpinnerService } from '../../components/spinner/event.spinner.service';

import { Menus } from '../../AtPar/Menus/routepath';

declare var module: {
    id: string;
}
@Component({

    selector: 'trackit-leftbar-cmp',
    template: require('./trackit.leftbar.component.html'),
    providers: []

})

export class TrackITLeftBarComponent implements OnInit {

    sideMenus: Menus[] = [];

    constructor(
        private spinnerService: SpinnerService,
        private router: Router
    ) {

    }

    ngOnInit() {
        this.getSideMenus();
    }

    async getSideMenus() {
        try {
            this.sideMenus = [];

            let menu: Menus = new Menus();
            menu.MENU_NAME = 'Create Request';
            menu.ROUTE = 'createrequest';
            menu.APP_ID = 100;
            menu.APP_NAME = 'TrackIT';
            this.sideMenus.push(menu);

            menu = new Menus();
            menu.MENU_NAME = 'Request Status';
            menu.ROUTE = 'requeststatus';
            menu.APP_ID = 100;
            menu.APP_NAME = 'TrackIT';
            this.sideMenus.push(menu);

            menu = new Menus();
            menu.MENU_NAME = 'Requestor Item Visibility Report';
            menu.ROUTE = 'requestoritemvisibilityreport';
            menu.APP_ID = 100;
            menu.APP_NAME = 'TrackIT';
            this.sideMenus.push(menu);

            menu = new Menus();
            menu.MENU_NAME = 'View Cart';
            menu.ROUTE = 'viewcart';
            menu.APP_ID = 100;
            menu.APP_NAME = 'TrackIT';
            this.sideMenus.push(menu);

            //this.sideMenus = [
            //    { MENU_NAME: 'Create Request', ROUTE: 'createrequest', GROUP_ID:100, },
            //    { MENU_NAME: 'Request Status', ROUTE: 'requeststatus' },
            //    { MENU_NAME: 'Requestor Item Visibility Report', ROUTE: 'requestoritemvisibilityreport' },
            //    { MENU_NAME: 'View Cart', ROUTE: 'viewcart' }
            //];
        } catch (ex) {

        }
    }

    async onMenuClick(menu: Menus)
    {
        localStorage.setItem("breadSubMenuName", '');
        localStorage.setItem("breadMenuName", '');  
        this.spinnerService.emitChangeBreadCrumbEvt(JSON.stringify(menu));
        //localStorage.setItem("submenu", JSON.stringify(menu));
        this.router.navigate(['trackitdashboard/' + menu.ROUTE]);

    }

    OnDestroy() {

    }

}



